.blog {
   padding: 60px 0 66px 0;

   &__inner {
      display: flex;
      justify-content: space-between;
      
   }

   &__items {
      width: 730px;
   }

   // &__item {
   // }

   &__item-img {
      margin-bottom: 25px;
   }

   &__item-info {
      color: #707070;
      font-weight: 600;
      margin-bottom: 9px;
   }

   &__item-infodate {
      color: #707070;
      font-weight: 600;
   }

   &__item-infoauthor {
      color: #707070;
      font-weight: 600;
   }

   &__item-infotitle {
      color: #707070;
      font-weight: 600;
   }

   &__item-title {
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 24px;
      line-height: 38px;
      color: #363838;
      &--first{
         margin-bottom: 6px;
      }
      &--second{
         margin-bottom: 93px;
      }
      &--third{
         margin-bottom: 3px;
      }
   }

   &__item-text {
      color: #5A5A5A;
      &--top{
         margin-bottom: 122px;
      }
      &--bottom{
      margin-bottom: 50px;
      }
   }

   &__blockquote {
      padding: 54px 64px;
      background: rgba(223, 223, 223, 0.5);
      margin-bottom: 50px;
  
   }
   &__blockquote-text{
      font-family: 'Open Sans';
      font-style: italic;
      font-weight: 300;
      font-size: 24px;
      line-height: 38px;
      text-align: center;
      color: #363838;
      padding-bottom: 23px;
      margin-bottom: 23px;
      position: relative;
      &::after{
         content: "";
         position: absolute;
         width: 200px;
         height: 1px;
         border-radius: 0.5px;
         background: #6D7278;
         bottom: 0;
         right: 0;
         left: 0;
         margin: 0 auto;
      }

   }
   &__blockquote-author {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      text-align: center;
      color: rgba(54, 56, 56, 0.9033);
   }

   // &__popup-play {
  
   // }

   // &__popup-imgbtn {
   
   // }

   // &__item-title--second {

   // }
}

.article-popup {
   background-image: url(../../../img/blog-popup-bg.jpg);
   background-repeat: no-repeat;
   height: 415px;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-bottom: 25px;

}
.article-slider {
   margin-bottom: 25px;
   // &__items {

   // }

   // &__item {
   // }

   // &__img {
   // }



.slick-arrow{
   border: none;
   outline: none;
   background-color: transparent;
   position: absolute;
   z-index: 5;
   transform: translateY(-50%);
   cursor: pointer;
   top: 50%;
 
 }
 .slick-prev{
   left: 16px;
 }
 .slick-next{
   right: 16px;
 }
 

}


.pagination {
   display: flex;
   justify-content: center;
   align-items: center;

   &__arrow {
      margin: 0 6px;
      &--disabled{
        opacity: 0;
        pointer-events: none;
      }
   }

   // &__arrow-icon {
   // }

   &__list {
      display: flex;
   }

   &__list-item {
      margin: 0 6px;
   }

   &__list-num {
      font-weight: 600;
      color: #707070;
   }

   &__list-num--active {
      color: #363838;
   }
}
