.custom-checkbox {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #a1a1a1;
  /* Цвет текста */
  cursor: pointer;
  user-select: none;
  margin-bottom: 21px;
}

.custom-checkbox input {
  display: none;
  /* Скрываем стандартный чекбокс */
}

.custom-checkbox .checkmark {
  width: 21px;
  height: 21px;
  background-color: transparent;
  /* Прозрачный фон по умолчанию */
  border: 2px solid #ccc;
  /* Серый бордер */
  border-radius: 4px;
  /* Скругленные углы */
  position: relative;
  margin-right: 8px;
  /* Отступ между чекбоксом и текстом */
  display: flex;
  align-items: center;
  /* Вертикальное выравнивание */
  justify-content: center;
  /* Горизонтальное выравнивание */
  flex-shrink: 0;
  box-sizing: border-box;
}

.custom-checkbox input:checked + .checkmark {
  background-color: #363838;
  /* Темный фон при активном состоянии */
  border-color: #363838;
  /* Убираем выделение бордера */
}

.custom-checkbox .checkmark::after {
  content: "";
  position: absolute;
  width: 8px;
  /* Ширина галочки */
  height: 12px;
  /* Высота галочки */
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  /* Поворот галочки */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  /* Центрирование галочки */
  opacity: 0;
  /* Галочка скрыта изначально */
}

.custom-checkbox input:checked + .checkmark::after {
  opacity: 1;
  /* Галочка видима при активном состоянии */
}

