.inspiration {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../img/inspiration-bg.jpg);
    background-repeat: no-repeat;
    height: 362px;
    z-index: -1;
  }
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__img {
    margin-right: 78px;
    margin-left: -230px;
  }

  &__content {
    max-width: 825px;
  }

  &__title {
    font-size: 36px;
    line-height: 60px;
    color: $title;
    @extend %title-font;
    padding-bottom: 16px;
    margin-bottom: 24px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      background: #6d7278;
      width: 200px;
      height: 1px;
    }
  }

  &__text {
    margin-bottom: 30px;
    color: #5a5a5a;
  }

  // &__list {
  // }

  &__item {
    color: $title;
    @extend %title-font;
    line-height: 23px;
    margin-bottom: 33px;
  }

  &__item-content {
    display: flex;
    justify-content: space-between;
    padding-bottom: 14px;
    border-bottom: 2px solid #fff;
    border-radius: 4px;
  }
  // &__item-text{

  // }

  // &__num {
  // }

  &__line {
    height: 2px;
    border-radius: 4px;
    background-color: $title;
    margin-top: -2px;
  }
}

.partners {
  padding: 16px 0 60px;
  &__items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  &__item {
    margin: 0 auto;
  }
}

.collection {
  padding-bottom: 70px;
  &__items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 30px;
  }
  &__item {
    position: relative;
    &:hover {
      .collection__info {
        opacity: 1;
      }
    }
  }

  &__img {
    max-width: 350px;
    object-fit: cover;
  }

  &__info {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#fff, 0.7);
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: all 0.5s;
  }

  &__info-title {
    font-size: 20px;
    line-height: 25px;
    color: $title;
    padding-bottom: 19px;
    margin-bottom: 19px;
    position: relative;
    &::after {
      content: "";
      bottom: 0;
      position: absolute;
      background: #6d7278;
      height: 1px;
      width: 200px;
      margin: 0 auto;
      left: 0;
      right: 0;
    }
  }

  &__info-text {
    font-weight: 600;
    font-size: 14px;
    line-height: 28px;
    color: #707070;
  }
}
